<template>
    <nav class="fixed top-0 left-0 flex flex-col items-center w-full h-32 px-4 bg-chinu-black-1 sm:flex-row sm:justify-between sm:h-20 sm:px-6 lg:px-10">
      <div class="flex items-center flex-shrink-0 mt-2 sm:mt-0">
        <img  
          class="block w-auto h-16 mt-2 mb-2 sm:mt-0 sm:mb-0"
          src="../assets/logo.png"
        />
      </div>
      <div class="flex items-center justify-between">
        <div v-if="isConnected && !initLoading" class="flex text-xs font-semibold text-white sm:text-base bg-chinu-red-2 bg-opacity-10">
          <div class="p-2.5 px-3">
            {{ start_and_end(web3.address) }}
          </div>
        </div>
        <button v-if="!isConnected" type="button" @click="init" class="inline-flex items-center px-4 py-2.5 ml-0 mt-2 sm:mt-0 sm:ml-6 text-xs font-bold text-chinu-white-1 rounded-md hover:text-white uppercase shadow-sm sm:text-base bg-chinu-red-3">
          Connect Wallet
          <Spinner v-if="initLoading" />
        </button>
        <button v-if="isConnected && initLoading" type="button" class="inline-flex items-center px-4 py-2.5 text-xs font-bold text-chinu-white-1 rounded-md hover:text-white uppercase shadow-sm sm:text-base bg-chinu-red-3">
          Connecting Wallet
          <Spinner />
        </button>
        <button v-else-if="isConnected" type="button" @click="disconnect" class="inline-flex items-center px-4 py-2.5 text-xs font-bold text-chinu-white-1 rounded-md hover:text-white uppercase shadow-sm sm:text-base bg-chinu-red-3">
          Disconnect Wallet
        </button>
      </div>
    </nav>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import Spinner from './Spinner.vue'
export default {
    name: "AppHeader",
    components: {
      Spinner
    },
    computed: {
      ...mapGetters([
        'isConnected',
        'initLoading',
        'activeNetwork',
        'web3'
      ])
    },
    methods: {
      ...mapActions([
        'init',
        'disconnect'
      ]),
      intToString(num) {
        if (num < 1000) {
            return num;
        }
        let si = [
          {v: 1E3, s: "K"},
          {v: 1E6, s: "M"},
          {v: 1E9, s: "B"},
          {v: 1E12, s: "T"},
          {v: 1E15, s: "P"},
          {v: 1E18, s: "E"}
          ];
        let i;
        for (i = si.length - 1; i > 0; i--) {
            if (num >= si[i].v) {
                break;
            }
        }
        return `${(num / si[i].v).toFixed(2).replace(/\.0+$|(\.[0-9]*[1-9])0+$/, "$1") + si[i].s}`;
      },
      start_and_end(str) {
        return str.substr(0, 6) + '...' + str.substr(str.length-4, str.length);
      }
    }
}
</script>