<template>
  <div class="relative flex flex-col w-full mx-auto top-44">
    <chinu-staking v-if="chinuStaking" :staking-contract="chinuStaking" />
    <div v-else class="relative flex items-center justify-center">
      <div class="flex flex-col w-full max-w-lg mx-auto text-white">
        <div class="py-5 text-center">
          <h1 class="sm:text-4xl text-2xl leading-relaxed font-chonk">CHINU Staking</h1>
          <p class="text-sm font-chonk">Stake your tokens to earn $CHINU</p>
        </div>
        <div class="bg-chinu-black-1 rounded-md">
          <div class="flex justify-between header-bg py-3 px-5 rounded-t-md">
            <div class="flex flex-col justify-center">
              <div class="flex items-center">
                <svg
                  width="14"
                  height="14"
                  viewBox="0 0 19 19"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M9.09699 0C4.07975 0 0.00779724 4.256 0.00779724 9.5C0.00779724 14.744 4.07975 19 9.09699 19C14.1142 19 18.1862 14.744 18.1862 9.5C18.1862 4.256 14.1142 0 9.09699 0ZM10.0059 14.25H8.18807V8.55H10.0059V14.25ZM10.0059 6.65H8.18807V4.75H10.0059V6.65Z"
                    fill="white"
                  />
                </svg>
                <h1 class="ml-1 text-lg font-bold">- Day Timelock</h1>
              </div>
              <h1 class="text-xs">Expiration: <span>-- -- --</span></h1>
            </div>
            <div class="text-center">
              <h1 class="text-lg font-bold">--%</h1>
              <p class="text-xs">APY</p>
            </div>
          </div>
          <div class="p-5">
            <button type="button" @click="switchNetwork" class="inline-flex items-center justify-center px-4 py-2.5 ml-0 mt-4 w-full text-xs font-bold text-white hover:text-white uppercase shadow-sm sm:text-base border border-chinu-red-4">
            Switch to ETH Network
          </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ChinuStaking from "./ChinuStaking.vue"
export default {
  name: "MainStaking",
  components: {
    ChinuStaking,
  },
  props: {
    filteredStakingContracts: {
      type: Array,
      default: []
    }
  },
  computed: {
    chinuStaking() {
      return this.filteredStakingContracts.filter(cc => {
        return cc.tokenAddy == "0xF36750F9eB97e2b5A8DfD883176075d915a5D3dC"
      })[0]
    },
  },
  methods: {
    async switchNetwork() {
      await window.ethereum.request({
        method: "wallet_switchEthereumChain",
        params: [
          {
            chainId: `0x1`,
          },
        ],
      });
    }
  }
}
</script>
