<template>
  <div class="relative min-h-screen min-w-screen font-tusker">
    <App-Header />
    <div
      v-if="isLoadingLocal"
      class="relative flex items-center justify-center top-56"
    >
      <div class="flex flex-col w-full max-w-lg mx-auto text-white">
        <div class="py-5 text-center">
          <h1 class="sm:text-4xl text-2xl leading-relaxed font-chonk">CHINU Staking</h1>
          <p class="text-sm font-chonk">Stake your tokens to earn $CHINU</p>
        </div>
        <div class="bg-chinu-black-1 rounded-md">
          <div class="flex justify-between header-bg py-3 px-5 rounded-t-md">
            <div class="flex flex-col justify-center">
              <div class="flex items-center">
                <svg
                  width="14"
                  height="14"
                  viewBox="0 0 19 19"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M9.09699 0C4.07975 0 0.00779724 4.256 0.00779724 9.5C0.00779724 14.744 4.07975 19 9.09699 19C14.1142 19 18.1862 14.744 18.1862 9.5C18.1862 4.256 14.1142 0 9.09699 0ZM10.0059 14.25H8.18807V8.55H10.0059V14.25ZM10.0059 6.65H8.18807V4.75H10.0059V6.65Z"
                    fill="white"
                  />
                </svg>
                <h1 class="ml-1 text-lg font-bold">- Day Timelock</h1>
              </div>
              <h1 class="text-xs">Expiration: <span>-- -- --</span></h1>
            </div>
            <div class="text-right pr-2">
              <h1 class="text-lg font-bold">--%</h1>
              <p class="text-xs">APY</p>
            </div>
          </div>
          <div class="p-5">
            <button
              v-if="!isConnected"
              type="button"
              @click="init"
              class="inline-flex items-center justify-center px-4 py-2.5 mt-2 w-full text-xs font-bold text-chinu-white-1 hover:text-white uppercase shadow-sm sm:text-base bg-chinu-red-4"
            >
              Connect Wallet
            </button>
            <button
              v-if="isConnected && initLoading"
              type="button"
              class="inline-flex items-center justify-center px-4 py-2.5 mt-2 w-full text-xs font-bold text-chinu-white-1 hover:text-white uppercase shadow-sm sm:text-base bg-chinu-red-4"
            >
              Connecting Wallet...
            </button>
          </div>
        </div>
      </div>
    </div>
    <main-staking
      v-else
      :filtered-staking-contracts="filteredStakingContracts"
    />
    <footer class="relative text-base text-center text-white top-72">
      <a
        href="https://oklg.io/"
        target="_blank"
        class="flex items-center justify-center"
      >
        Powered by OKLG
      </a>
    </footer>
    <notifications position="top right" />
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import BigNumber from "bignumber.js";
import AppHeader from "./components/AppHeader.vue";
import MainStaking from "./components/MainStaking.vue";
import Spinner from "./components/Spinner.vue";
export default {
  components: {
    AppHeader,
    MainStaking,
    Spinner,
  },
  data() {
    return {
      isLoading: false,
      isLoadingLocal: true,
    };
  },
  computed: {
    ...mapGetters([
      "isConnected",
      "initLoading",
      "activeNetwork",
      "web3",
      "selectedTokenAddress",
      "tokenStakingContracts",
      "currentBlock",
    ]),
    smartContractType() {
      if (this.isConnected && !this.initLoading) {
        if (this.activeNetwork.network_id == 1) {
          return "ERC20";
        } else if (this.activeNetwork.network_id == 56) {
          return "BEP20";
        }
      } else {
        return "ERC20";
      }
    },
    blocksPerDay() {
      return this.activeNetwork ? this.activeNetwork.blocks_per_day : 0;
    },
    isAddyValid() {
      return (
        this.selectedTokenAddress &&
        this.web3.instance &&
        this.web3.instance.utils.isAddress(this.selectedTokenAddress)
      );
    },
    filteredStakingContracts() {
      return this.tokenStakingContracts
        .slice(0)
        .filter((c) => {
          return (
            (c.lastStakableBlock &&
              new BigNumber(c.lastStakableBlock).gt(this.currentBlock)) ||
            new BigNumber(c.farmingTokenBalance).gt(0)
          );
        })
        .filter(
          (c) =>
            !(
              c.contractIsRemoved && new BigNumber(c.farmingTokenBalance).lte(0)
            )
        );
    },
    filterStakingLength() {
      return this.filteredStakingContracts.length;
    },
  },
  watch: {
    async isAddyValid(isValid) {
      if (isValid) await this.lookUpTokenStakingContracts();
    },
    initLoading(value) {
      this.isLoadingLocal = value;
    },
    isConnected(value) {
      if (!value) {
        this.isLoadingLocal = true;
      }
    },
  },
  async mounted() {
    await this.init(true);
    await this.lookUpTokenStakingContracts();
  },
  methods: {
    ...mapActions(["init", "disconnect", "getAllStakingContracts"]),
    userBalanceFormatted(tokenInfo) {
      return new BigNumber(tokenInfo.userBalance)
        .div(new BigNumber(10).pow(tokenInfo.decimals))
        .toFormat(2);
    },
    async lookUpTokenStakingContracts() {
      try {
        this.isLoadingLocal = true;
        await this.getAllStakingContracts();
      } catch (err) {
        console.log(err);
      } finally {
        this.isLoadingLocal = false;
      }
    },
  },
};
</script>

