<template>
  <div
    class="fixed inset-0 z-10 overflow-y-auto"
    aria-labelledby="modal-title"
    role="dialog"
    aria-modal="true"
  >
    <div
      class="flex items-end justify-center min-h-screen px-4 pt-4 pb-20 text-center sm:block sm:p-0"
    >
      <div
        class="fixed inset-0 transition-opacity bg-black bg-opacity-90"
        aria-hidden="true"
      >
        <div class="relative flex items-center justify-center top-32">
          <h1 class="text-6xl font-bold text-white">Stake Your Tokens</h1>
        </div>
      </div>
      <span
        class="hidden sm:inline-block sm:align-middle sm:h-screen"
        aria-hidden="true"
        >&#8203;</span
      >
      <div
        class="relative inline-block overflow-hidden text-left align-bottom transition-all transform bg-chinu-black-1 rounded-md shadow-xl sm:my-8 sm:align-middle sm:max-w-2xl sm:w-full"
      >
        <div class="flex justify-end p-3 item-center">
          <button
            type="button"
            @click="$emit('close-modal')"
            class="text-gray-400 bg-transparent rounded-md"
          >
            <span class="sr-only">Close</span>
            <!-- Heroicon name: outline/x -->
            <svg
              class="w-6 h-6 text-white"
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              aria-hidden="true"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="4"
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          </button>
        </div>
        <div v-if="isLoadingLocal" class="flex flex-col items-center justify-center p-10">
          <img src="../assets/logo.png" alt="">
          <h1 class="mt-5 text-2xl font-bold text-center text-white">FETCHING DATA...</h1>
        </div>
        <div v-else class="flex flex-col px-10 pb-5">
          <div class="flex items-center justify-center text-xs text-white">
            <h1 class="text-base">Enter the amount you want to stake</h1>
          </div>
          <div class="relative my-3 border border-chinu-red-4 rounded-full">
            <div class="absolute inset-y-0 left-0 flex items-center pl-4 pointer-events-none">
              <span class="text-sm font-bold text-white text-opacity-60">
                $CHINU
              </span>
            </div>
            <input v-model="swapAmount" type="text" name="price" id="price" class="block w-full pt-5 pb-5 text-sm font-bold text-center text-white placeholder-white bg-transparent outline-none rounded-xl px-28 placeholder-opacity-60 " placeholder="Enter the amount you want to stake">
            <button
              @click="maxAmount"
              class="absolute inset-y-0 right-0 flex items-center pl-5 pr-5 text-white bg-transparent"
            >
              Max
            </button>
          </div>
          <div class="flex items-center justify-center text-xs text-white">
            <h1 class="text-base">$CHINU Balance: <span class="font-bold">{{ userStakingBalance }}</span></h1>
          </div>
          <div class="flex items-center mt-10">
            <button 
              type="button" 
              class="inline-flex items-center justify-center w-3/4 p-4 mr-10 text-sm font-bold text-white shadow-sm rounded-full bg-chinu-red-4 hover:bg-opacity-80"
              :class="{
                'cursor-not-allowed bg-king-dark': isLoading
              }"
              :disabled="isLoading"
              @click="stakeTokens"
            >
              Stake
            </button>
            <button 
              type="button" 
              class="inline-flex items-center justify-center w-1/4 p-4 text-sm font-bold text-white bg-black border shadow-sm border-chinu-red-4 rounded-full hover:border-gray-500"
              :class="{
                'cursor-not-allowed bg-king-dark': !isPastTimelock
              }"
              :disabled="!isPastTimelock"
              @click="unstakeTokens"
            >
              Unstake
            </button>
          </div>
          <template v-if="hasStakedTokens">
            <div v-if="!isPastTimelock">
              <p class="mt-5 font-semibold text-justify text-white">
                This farm has a {{ timelockDays }} day timelock. You originally staked at {{ timeUserOriginallyStaked }} and will be able to unstake your tokens after {{ timeUserCanUnstake }}.
              </p>
            </div>
          </template>
          <a
            href="https://oklg.io/"
            target="_blank"
            class="mt-8 mb-3 text-sm font-bold text-center text-white cursor-pointer text-opacity-90 hover:text-opacity-80"
          >
            Powered by OKLG
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex"
import BigNumber from "bignumber.js";
import dayjs from "dayjs";

export default {
  name: "ChinuModal",
  components: {
  },
  props: ["stakingContract", "isExpired"],
  data() {
    return {
        isLoadingLocal: true,
        isLoading: false,
        swapAmount: null,
        swapPercentage: 0,
        stakingInfo: {},
        selectedNftTokenIds: []
    };
  },
  computed: {
    filteredStakingContracts() {
      return this.stakingContract
    },
    farmingTokenAddress() {
      return this.stakingContract.farmingTokenAddy;
    },
    tokenDecimals() {
      return this.stakingContract.currentTokenDecimals
    },
    canStake() {
      return this.stakingInfo.poolInfo.isStakedNft
        ? this.selectedNftTokenIds.length > 0
        : this.swapAmount > 0;
    },
    userStakingBalance() {
      return this.stakingInfo.poolInfo.isStakedNft
        ? this.stakingInfo.stakingTokenInfo.userBalance
        : new BigNumber(this.stakingInfo.stakingTokenInfo.userBalance)
            .div(
              new BigNumber(10).pow(this.stakingInfo.stakingTokenInfo.decimals)
            )
            .toFormat(0, BigNumber.ROUND_DOWN);
    },
    isPastTimelock() {
      if (!this.stakingInfo) return true;
      const timelockSeconds = this.stakingInfo.poolInfo.stakeTimeLockSec;
      if (!timelockSeconds) return true;
      const userStakedTime =
        this.stakingInfo.stakerInfo &&
        this.stakingInfo.stakerInfo.timeOriginallyStaked;
      if (!userStakedTime) return true;
      const isPastTime = dayjs(
        new BigNumber(userStakedTime).times(1e3).toNumber()
      )
        .add(timelockSeconds, "seconds")
        .isBefore(dayjs());
      if (isPastTime) return true;
      if (this.isExpired) return true;
      return false;
    },
    contractIsRemoved() {
      return this.stakingInfo.contractIsRemoved;
    },
    timelockDays() {
      const timelockSeconds = this.stakingInfo.poolInfo.stakeTimeLockSec;
      if (!timelockSeconds) return 0;
      return new BigNumber(timelockSeconds).div(60).div(60).div(24).toFormat();
    },
    hasStakedTokens() {
      return new BigNumber(this.stakingInfo.userStakingAmount).gt(0);
    },
    timeUserOriginallyStaked() {
      const userStakedTime = this.stakingInfo.stakerInfo.timeOriginallyStaked;
      return dayjs(new BigNumber(userStakedTime).times(1e3).toNumber()).format(
        "MMMM DD, YYYY HH:mm:ss"
      );
    },
    timeUserCanUnstake() {
      const timelockSeconds = this.stakingInfo.poolInfo.stakeTimeLockSec;
      const userStakedTime = this.stakingInfo.stakerInfo.timeOriginallyStaked;
      return dayjs(new BigNumber(userStakedTime).times(1e3).toNumber())
        .add(timelockSeconds, "seconds")
        .format("MMMM DD, YYYY HH:mm:ss");
    },
  },
  async mounted() {
    try {
      this.stakingInfo = await this.getFaasStakingInfo(this.stakingContract.farmingTokenAddy)
    } catch(err) {
      console.log(err)
    } finally {
      this.isLoadingLocal = false;
    }
  },
  methods: {
    ...mapActions([
      "init",
      "getFaasStakingInfo",
      "faasStakeTokens",
      "faasUnstakeTokens"
    ]),
    maxAmount() {
      this.swapAmount = new BigNumber(this.stakingContract.currentTokenBalance)
        .div(new BigNumber(10).pow(this.tokenDecimals))
        .toFormat(2).replace(/,/g,'')
    },
    async stakeTokens() {
      try {
        if (!this.canStake) return;
        this.isLoading = true
        const amountToFormat = new BigNumber(this.swapAmount.replace(/,/g, ""));
        const correctSendTokenAmount = new BigNumber(amountToFormat)
          .times(new BigNumber(10).pow(this.tokenDecimals))
          .toFixed()
        await this.faasStakeTokens({
          farmingContractAddress: this.farmingTokenAddress,
          stakingContractAddress: this.stakingInfo.stakingTokenInfo.address,
          amountTokens: correctSendTokenAmount,
          nftTokenIds: this.selectedNftTokenIds,
        })
        this.$notify({
          type: "success",
          text: "Successfully staked your tokens!",
        });
        await this.init(true)
      } catch (err) {
        this.$notify({
          type: "error",
          text: err.message,
        });
      } finally {
        this.isLoading = false
      }
    },
    async unstakeTokens() {
      try {
          this.isLoading = true
          await this.faasUnstakeTokens({
            farmingContractAddress: this.farmingTokenAddress,
          })
        this.$notify({
          type: "success",
          text: "Successfully unstaked all tokens!",
        });
        await this.init(true)
      } catch (err) {
        console.error("Error unstaking tokens", err);
        this.$notify({
          type: "error",
          text: err.message,
        });
      } finally {
        this.isLoading = false
      }
    }
  },
};
</script>
